export default {
	data() {
		return {
			tip: '',
			isShowTip: true,
			hideTimer: null,
			noneTimer: null,
			isShow: false
		};
	},
	props:{
		time:{
			type:[String,Number],
			default:3200,
		}
	},
	computed: {
		_time(){
			return 'animation-duration:' + this.time + 'ms;';
		}
	},
	methods: {
		show(tip) {
			clearTimeout(this.noneTimer);
			this.isShow = false;
			this.$nextTick(()=>{
				this.tip = tip;
				this.isShow = true;
				this.noneTimer = setTimeout(() => (this.isShow = false), parseInt(this.time));
			});
		}
	}
};
